body{
  margin: 0;
  border: 0;
  padding: 0;
  background-image: -webkit-radial-gradient(top,circle,#FCF9F9, #B0B0B3);
  background-image: radial-gradient(circle at top,#FCF9F9, #B0B0B3);
  background-repeat: no-repeat;
  background-size: cover;
  min-height: 100vh;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}
